@import url('https://fonts.googleapis.com/css?family=Open+Sans|Pacifico');

body {
  margin: 0;
}

p {
  margin: 0;
  display: inline-block;
}

.App {
  background-color: #F5F5F5;
  height: 100vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  padding: 50px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.chatroom {
  background-color: rgba(0,0,0, .5);
  border-radius: 5px;
  overflow: hidden;
  -ms-flex: 1 1;
      flex: 1 1;
  -webkit-box-shadow: 0 0 16px 0 rgba(0,0,0, 0.3);
          box-shadow: 0 0 16px 0 rgba(0,0,0, 0.3);
}

.chatroom h3 {
  font-family: 'Roboto';
  background-color: #FC5830;
  color: #FFF;
  margin: 0;
  height: 40px;
  font-size: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.chatroom .chats {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 20px;
  margin: 10px 0 0;
  height: 510px;
  overflow-y: scroll;
}

.chatroom .chats .chat {
  background: rgba(255, 255, 255, 0.8);
  position: relative;
  padding: 5px 13px;
  font-size: 14px;
  border-radius: 10px;
  list-style: none;
  float: left;
  clear: both;
  margin: 10px 0;
  max-width: 200px;
}

.chatroom .chats .chat img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  top: -10px;
  left: -10px;
}

.chatroom .chats .chat.right {
  float: right;
  clear: both;
}

.chatroom .chats .chat a {
  text-decoration: none;
  color:#3498DB;
}

.button  {
  display: -ms-flexbox;
  display: flex;
  background: #FC5830;
  -ms-flex-pack: center;
      justify-content: center;
  color:#fff;
  font-size: 14px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.button:hover {
  background: #F5F5F5;
  color: #000;
}

.chatroom .input {
  display: -ms-flexbox;
  display: flex;
  background: #FC5830;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: right;
      justify-content: right;
}

.chatroom .input input[type="text"] {
  background: rgba(255, 255, 255, 0.5);
  font-size: 14px;
  width: 100%;
}

.chatroom .input input[type="submit"] {
  display: -ms-flexbox;
  display: flex;
  background: transparent;
  font-size: 14px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: #fff;
}

.chatroom .input input[type="submit"]:hover {
  background: #F5F5F5;
  color: #000;
}



